export const string = "<div class=\"flex-wrap\"><fieldset class=\"flex-1 border-dotted border-grey-2x border\"><div [(screenWidthModel)]=\"screenWidthModel\"></div><legend class=\"pad-h\"><h3 class=\"margin-0\">screenWidthModel</h3></legend><p class=\"text-grey-2x\">Get the browser window inner width. Currently : {{screenWidthModel}}px</p><div class=\"text-center\"><div class=\"border border-info pad-xs\" (click)=\"viewScreenWidthModel=!viewScreenWidthModel\" [ngClass]=\"{'border-energized bg-energized':viewScreenWidthModel}\">view details</div></div><div *ngIf=\"viewScreenWidthModel\" [@500]=\"'fadeInUp'\"><h4>Usage Example</h4><pre class=\"code-sample\">&lt;div(\"[(screenWidthModel)]\"=\"screenWidthModel\")&gt;"+
"\n  |screenWidthModel : {{ screenWidthModel }}px"+
"\n&lt;/div&gt;"+
"\n</pre></div></fieldset><fieldset class=\"flex-1 border-dotted border-grey-2x border\"><div [(screenHeightModel)]=\"screenHeightModel\"></div><legend class=\"pad-h\"><h3 class=\"margin-0\">screenHeightModel</h3></legend><p class=\"text-grey-2x\">Get the browser window inner height. Currently : {{screenHeightModel}}px</p><div class=\"text-center\"><div class=\"border border-info pad-xs\" (click)=\"viewScreenHeightModel=!viewScreenHeightModel\" [ngClass]=\"{'border-energized bg-energized':viewScreenHeightModel}\">view details</div></div><div *ngIf=\"viewScreenHeightModel\" [@500]=\"'fadeInUp'\"><h4>Usage Example</h4><pre class=\"code-sample\">&lt;div(\"[(screenHeightModel)]\"=\"screenHeightModel\")&gt;"+
"\n  |screenHeightModel : {{ screenHeightModel }}px"+
"\n&lt;/div&gt;"+
"\n</pre></div></fieldset><fieldset class=\"flex-1 border-dotted border-grey-2x border\"><legend class=\"pad-h\"><h3 class=\"margin-0\">route-doc-watcher</h3></legend><p class=\"text-grey-2x\">Get in tune with your document route states</p><div class=\"text-center\"><div class=\"border border-info pad-xs\" (click)=\"viewRouteDocWatcher=!viewRouteDocWatcher\" [ngClass]=\"{'border-energized bg-energized':viewRouteDocWatcher}\">view details</div></div><div *ngIf=\"viewRouteDocWatcher\" [@500]=\"'fadeInUp'\"><div class=\"pad\">Requirements<ul><li>ui-router-ng2</li></ul></div><h4>Usage Example</h4><pre class=\"code-sample\">&lt;route-doc-watcher [(ref)]=\"\" (beforeChange)=\"\" (onChange)=\"\"&gt;</pre><h4>ref example</h4><pre class=\"code-sample max-height-500\">&lt;route-doc-watcher [(ref)]=\"routeState\" &gt;"+
"\n-----------------------------------------"+
"\nrouteState:{{ routeDocWatcher|json }}</pre></div></fieldset></div>"